<script>

    import _ from 'lodash';
    import $ from 'jquery';
    import 'jquery.easing';

    //import 'slick-carousel';
    import 'slick-carousel/slick/slick.min'
    import 'slick-carousel/slick/slick.css'

    import sectionsToNavItems from './mappers/sectionsToNavItems';

    import sectionNav from './components/sectionNav';

    export default {
        name: 'app',
        data() {
            return {
                sections: [],
                debugMode: false
            }
        },
        components: {
            sectionNav
        },
        computed: {
            navItems() {
                return sectionsToNavItems(this.sections);
            }
        },
        mounted() {

            if(localStorage.getItem('debugMode')) {
                try {
                    this.debugMode = JSON.parse(localStorage.getItem('debugMode'));
                } catch(e) {}
            }
            document.addEventListener('dblclick', () => {
                this.debugMode = !this.debugMode;
                localStorage.setItem('debugMode', JSON.stringify(this.debugMode));
            });




            $('.slider-container').slick({
                slidesToShow: 1,
                arrows: true,
                dots: true,
                rows: 0,
                prevArrow: '<i class="pa-l d-grey ifb-caret-left fs-58 cursor-pointer v-centered z-top m-l--60"></i>',
                nextArrow: '<i class="pa-r d-grey ifb-caret-right fs-58 cursor-pointer v-centered m-r--60"></i>',
            });




            const self = this;

            const sections = [...document.querySelectorAll('.section')];

            for(const section of sections) {
                this.sections.push({
                    sectionId: section.id,
                    title: '',
                    el: section,
                    isInView: false
                })
            }

            this.$on('nav', (el) => {
                doScroll(el);
            })


            const scrollDuration = 600;
            //const scrollbarWidth = window.innerWidth - document.documentElement.clientWidth;
            let scrollDir = 'down';
            let scrollPos = 0;

            let react = true;
            let isScrolling = false;

            let shouldDebounce = false;

            const debounceFn = _.debounce(() => {
                react = true;
            }, 100);

            window.addEventListener('scroll', () => {
                return; //if(window.innerWidth < 1300 || window.innerHeight < 800) return;

                const newScrollDir = window.pageYOffset >= scrollPos  ? 'down' : 'up';

                if(newScrollDir !== scrollDir) {
                    react = true;
                    debounceFn.cancel();
                }

                if(!react) {
                    debounceFn();
                    if(!isScrolling) {
                        window.scrollTo({
                            top: scrollPos,
                            left: 0,
                            behavior: 'auto'
                        });
                    }
                    return;
                }


                scrollDir = newScrollDir;
                scrollPos = window.pageYOffset;

                doScroll(resolveScrollTarget());
            });

            function resolveScrollTarget() {
                const currSectEl = findCurrentSection();
                const currSectIndex = sections.indexOf(currSectEl);

                if(currSectIndex !== -1) {
                    const nextSectEl = scrollDir === 'down' ? sections[currSectIndex] : sections[currSectIndex-1];
                    return nextSectEl
                }
            }

            function doScroll(nextSectEl) {
                if(isScrolling) return;
                if(nextSectEl) {
                    react = false;
                    isScrolling = true;

                    for(const sectionIndex in self.sections) {
                        self.sections[sectionIndex].isInView = self.sections[sectionIndex].sectionId === nextSectEl.id;
                    }

                    $('html, body').animate({
                        scrollTop: nextSectEl.offsetTop
                    }, scrollDuration, 'easeInOutCubic', function() {
                        scrollPos = window.pageYOffset;

                        setTimeout(() => {
                            shouldDebounce = true;
                            isScrolling = false;
                        })
                    });
                }
            }

            function findCurrentSection() {
                for(const section of sections) {
                    if(section.offsetTop >= scrollPos) return section;
                }
            }

        },
        methods: {
            nav(e) {
                this.$emit('nav', e.el);
            }
        }
    }
</script>

