
function sectionsToNavItems(sections) {
    let arr = [];
    if(Array.isArray(sections)) {
        for(const section of sections) {
            arr.push({
                id: section.sectionId,
                title: section.title.join(' '),
                isInView: section.hasMajorityScreenEstate
            })
        }
    }
    return arr;
}

export default sectionsToNavItems